.auth__button {
  padding: 0;
  width: 100%;
  min-height: 50px;
  cursor: pointer;
  margin: 170px auto 0px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  border-radius: 2px;
  border: none;
  background-color: white;
  transition: 0.2s;
}

.auth__button:hover {
  opacity: 0.85;
}
