.footer {
  max-width: 880px;
  margin: 0 auto;
  padding: 66px 0 60px 0;
}

@media screen and (max-width: 1023px) {
  .footer {
    max-width: 580px;
  }
}

@media screen and (max-width: 767px) {
  .footer {
    max-width: 282px;
    padding-top: 48px;
    padding-bottom: 36px;
  }
}
